<template>
  <div>
    <!-- MODAL -->
    <button @click="formModal=!formModal;formAction='add';vdata={}" type="button" class="btn btn-sm btn-dark rounded-circle" 
    style="position:fixed;bottom:20px;right:20px;z-index:100;height:40px;width:40px;">+</button>
    <div v-if="formModal" @click="formModal=false" style="position:fixed;width:100vw;height:100vh;left:0;top:0;z-index:100;background:black;opacity:0.5;"></div>
    <div v-if="formModal" style="position: fixed;top: 50%;left: 50%;transform: translateX(-50%) translateY(-50%);z-index:105;min-width:30vw;">
        <div class="rounded-lg shadow p-3 bg-white animate__animated animate__bounceIn">
          <div class="p-4 font-semibold">
              Add Layouts
          </div>
          <hr>
          <form action="" @submit.prevent="submitLayout">
            <input required type="text" id="nama_doc" name="nama_doc" class="form-control p-2 rounded-lg shadow mt-3" placeholder="nama_doc" v-model="vdata['nama_doc']" >
            <div class="sm-form">
            <textarea required type="text" id="deskripsi" name="deskripsi" rows="2" placeholder="deskripsi..." class="form-control md-textarea mt-2" v-model="vdata.deskripsi" ></textarea>
            </div>
            <input required type="text" id="table" name="table" class="form-control p-2 rounded-lg shadow mt-3" placeholder="Nama table" v-model="vdata['table']" >
            <input required type="file" id="filenya" name="filenya" class="form-control p-2 rounded-lg shadow mt-3" placeholder="filenya" @change="upload" >
             <select required class='form-control mt-2' v-model="vdata.dimension">
              <option value="potrait">Potrait</option>
              <option value="landscape">Landscape</option>
            </select>
            <div class="text-right">
              <button type="submit" class="btn btn-sm btn-outline-primary text-sm mt-3" v-if="formAction=='add'">Simpan</button>
              <button type="submit" class="btn btn-sm btn-outline-success text-sm mt-3" v-else>Edit</button>
            </div>
          </form>
        </div>
    </div>
        <!-- MODAL -->
    
    <div class="row test" style="min-height:60vh;" >
      <div class="col-12 p-4 mt-2 ">
        <div class="row justify-content-center">
          <div class="col-7 pl-4">
            <div class="sm-form ">
                <input type="text" id="search" name="search" class="form-control p-2 rounded-lg shadow" placeholder="search" v-model="search" >
            </div>
          </div>
          <div class="col-4 col-sm-2">
            <button type="button" class="btn btn-sm btn-dark rounded-lg btn-block p-2" @click="formModal=!formModal;formAction='add';vdata={}">Add New +</button>
          </div>
        </div>
        <!--  -->
        <div class="row px-5 py-3">
          <div class="col-sm-4 px-2"  v-for="(item, index) in datanya" :key="index+'datanya'">
            <div class="p-4 card-border rounded-lg shadow-lg text-sm">
              <button type="button" @click="removeData(item)" class="btn btn-sm btn-danger text-xs float-right"><i class="fa fa-trash-o" aria-hidden="true"></i> Remove</button>
              <div >
                <span class="text-xs">{{item.nama_doc}}</span>
                <!-- <span class="ml-2 text-xs">{{item._id}}</span> -->
              </div>
              <hr>
              <div class="text-xs font-semibold float-right">
                <div>Height : {{item.height}}</div>
                <div>Width : {{item.width}}</div>
              </div>
              <div class="text-xs" :class="item.deskripsi?'text-gray-600':''">
                {{item.deskripsi??'No Deskripsi'}} 
              </div>
              <img v-viewer :src="`https://hit-ocr.online/uploaded/${item.filename}`" style="width:30px;height:40px;" alt="">
              <br>
              <hr>
              <div class="row">
                <div class="col-3">
                  <button type="button"  class="btn btn-sm btn-outline-dark btn-block text-xs" @click="$router.push(`/database?id=${item._id}`)">
                  <i class="fa fa-paint-brush" aria-hidden="true"></i>
                  </button>
                </div>
                <div class="col-3">
                  <button type="button"  class="btn btn-sm btn-dark btn-block text-xs" @click="$router.push(`/draw?id=${item._id}`)">
                  <i class="fa fa-eye" aria-hidden="true"></i>
                  </button>
                </div>
                <div class="col-3">
                  <button type="button" @click="$router.push(`/scan?id=${item._id}`)" class="btn btn-sm btn-primary btn-block text-xs">
                  <i class="fa fa-camera-retro" aria-hidden="true"></i></button>
                </div>
                <div class="col-3">
                  <button type="button" @click="formModal=true;formAction='update';vdata=JSON.parse(JSON.stringify(item))" class="btn btn-sm btn-success btn-block text-xs">
                  <i class="fa fa-pencil" aria-hidden="true"></i>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!--  -->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      vdata:{
      },
      formAction:'add',
      datanya:[],
      search:'',
      formModal:false
    }
  },
  methods: {
    async upload(e){
      let that=this;
      let file=e.target.files[0]
      const newName = this.vdata.nama_doc + e.target.files[0].name;
      const modifiedFile = new File([e.target.files[0]], newName, { type: e.target.files[0].type });
      const dimensions = await this.getDimensionsFromFile(modifiedFile);
      this.vdata.height=dimensions.height
      this.vdata.width=dimensions.width
      if(dimensions.height>dimensions.width){
        this.vdata.dimension='potrait'
      }else{
        this.vdata.dimension='landscape'
      }
      this.$forceUpdate()
      let fd = new FormData()
      fd.append("file",modifiedFile)
      axios.post("https://hit-ocr.online/api/upload/image",fd).then(res=>{
        that.vdata.filename=file.name
        alert("Upload Berhasil...")
      })
    },
    getDimensionsFromFile(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = function (event) {
          const img = new Image();
          img.onload = function () {
            const width = img.width;
            const height = img.height;
            resolve({ width, height });
          };
          img.onerror = function () {
            reject("Error loading image.");
          };
          img.src = event.target.result;
        };
        reader.onerror = function () {
          reject("Error reading file.");
        };
        reader.readAsDataURL(file);
      });
    },
    getDimension(){
      if(this.vdata.dimension=='1'){
          this.vdata.height=1920
          this.vdata.width=1080
         }else if(this.vdata.dimension=='2'){
          this.vdata.height=1080
          this.vdata.width=1920
         }
    },
    async submitLayout(){
      if(this.formAction=='add'){
        const usersCollection = firebase.collection(firebase.firestore, 'ocr-layouts');
         this.formModal=false;
        //  this.getDimension()
         firebase.addDoc(usersCollection, this.vdata)
         .then((docRef) => {
           console.log('Layouts added with ID: ', docRef.id);
         })
         .catch((error) => {
           console.error('Error adding Layouts: ', error);
         });
      }else if(this.formAction=='update'){
        const docRef = firebase.doc(firebase.firestore, 'ocr-layouts', this.vdata._id);
        // this.getDimension()
        delete this.vdata._id
        firebase.updateDoc(docRef, this.vdata)
        .then(() => {
          console.log('Document updated successfully');
        })
        .catch((error) => {
          console.error('Error updating document:', error);
        });
      }
    },
    async removeData(item){
      if(confirm("Are you sure to delete?")){
        let doc=firebase.doc(firebase.firestore, "ocr-layouts", item._id)
        await firebase.deleteDoc(doc)
      }
    },
    async getData(){
        let that=this;
        const layouts = firebase.collection(firebase.firestore, 'ocr-layouts');
        firebase.onSnapshot(layouts, (querySnapshot) => {
          that.datanya=[]
          querySnapshot.forEach((doc) => {
            that.datanya.push({_id:doc.id,...doc.data()})
          });
        }, (error) => {
          console.error('Error getting real-time collection:', error);
        });
    },
  },
  mounted() {
    this.getData()
  },
}
</script>
